body {
  margin: 0;
  padding: 0;
  font-family: 'Nunito', sans-serif;
}

#submit {
    width: 100%;
    position: relative;
    bottom: 20px;
}

#statement {
    margin-top: 40px;
    color: white;
}

#message {
    height: 200px;
    position: relative;
    bottom: 20px;
    background: rgb(248, 247, 247, 0.7);
    border: black groove 2px;
}

#title {
    position: relative;
    bottom: 20px;
    background: rgb(248, 247, 247, 0.7);
    border: black groove 2px;
}








#titles {
    border-bottom: rgb(24, 12, 2) solid 2px;
    padding-bottom: 3px;
    font-size: 30px;
}

#messages {
    border-bottom: rgb(24, 12, 2) solid 2px;
    padding-bottom: 7px;
}
#comment-submit {
    width: 100%
}

#comment {
    height: 200px;
}
#comments {
    border-top: solid black 2px;
    border-bottom: solid black 2px;
    line-height: 100px;
    overflow: scroll;
    text-align: left;
    padding-left: 10px;
    padding-right: 10px;
    height: 400px;
}
.footer {
  /* left: 0;
  bottom: 0; */
  width: 100%;
  background: rgb(255, 114, 19);
  color: white;
  text-align: center;
  margin-top: 40px;
  border-top: rgb(240, 228, 228) solid 3px;
}

#copyright {
    position: relative;
    top: 10px;
}
/* .content {
    background-image: url('../../../sunset-1.jpg');
    background-size: cover;
} */

.comment-section {
    background: rgb(248, 247, 247, 0.7);
    width: 100%;
    text-align: center;
    padding-bottom: 10px;
    border-radius: 10px;
    border: black groove 2px;
    padding-left: 10px;
    padding-right: 10px;
    position: relative;
    bottom: 40px;
    padding-top: 10px;
}

.comment-display {
    border: solid rgb(255, 94, 0) 2px;
}



.jumbotron {
    background-image: url(/static/media/sunset-1.811d7688.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    height: 650px;
    color: white;
    border-bottom: rgb(240, 228, 228) solid 3px;
    margin-bottom: 10px;
}

#phocus {
    margin-top: 145px;
}





/* body {
    background: white;
} */

.everything {
    background: rgb(58, 57, 57);
    padding-top: 0;
}
.wrapper {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    margin-top: 20px;
    margin-left: 0;
    /* width: 1200px; */
    padding: 0 auto;
}
.card {
    border: 1px solid black;
    border-radius: 2px;
    height: 300px;
    margin: 0.5rem;
    width: 300px;
}

.card > .img-container {
    height: 100%;
    overflow: hidden;
}

.card > .img-container > img {
    width: 100%;
    height: 100%; 
}

#buttonone {
    opacity: 0.5;
}

#buttonone:hover {
    opacity: 1;
}

#buttontwo {
    opacity: 0.5;
}

#buttontwo:hover {
    opacity: 1;
}

.main-content {
    background: rgb(58, 57, 57);
    margin-bottom: 20px;
}

.navbar {
    background: rgb(255, 114, 19);
    color: white;
    border-bottom: white solid 2px;
    margin-bottom: 0;
}

